<template>
    <div>
        <el-container>
            <el-header class="homeHeader">
                <div class="title">微人事</div>
                <div>
                    <el-button icon="el-icon-bell" type="text" style="margin-right: 8px;color: #000000;"
                               size="normal" @click="goChat"></el-button>
                    <el-dropdown class="userInfo" @command="commandHandler">
                 <span class="el-dropdown-link">
                  {{user.name}}
                  </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="userinfo">个人中心</el-dropdown-item>
                            <!--//command:作为标记-->
                            <el-dropdown-item command="setting">设置</el-dropdown-item>
                            <el-dropdown-item command="logout">注销登录</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
            </el-header>
            <el-container>
                <el-aside width="200px">
                    <!--是否使用 vue-router 的模式，启用该模式会在激活导航时以 index 作为 path 进行路由跳转-->
                    <el-menu router="" unique-opened="">   <!--unique-opened:同一时间只打开一个菜单项-->

                        <!--循环遍历index.js的routes对象-->
                        <el-submenu :index="index+''" v-for="(item,index) in routes" v-if="!item.hidden"
                                    :key="index">
                            <template slot="title">
                                <i style="color: deepskyblue" :class="item.iconCls"></i>
                                <span>{{item.name}}</span>
                            </template>
                            <el-menu-item :index="child.path" v-for="(child,indexj) in item.children" :key="indexj">
                                <i :class="child.iconCls"></i>
                                {{child.name}}
                            </el-menu-item>
                        </el-submenu>
                    </el-menu>
                </el-aside>
                <el-main>
                    <el-breadcrumb separator-class="el-icon-arrow-right" v-if="this.$router.currentRoute.path!='/home'">
                        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
                        <el-breadcrumb-item>{{this.$router.currentRoute.name}}</el-breadcrumb-item>
                    </el-breadcrumb>
                    <div v-if="this.$router.currentRoute.path=='/home'" class="welcomeCls">欢迎来到微人事</div>
                    <router-view class="homeRouter"/>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>

    export default {
        name: "Home"
        , data() {
            return {
                user: JSON.parse(window.sessionStorage.getItem("user"))
            }

        },
        methods: {
            /*  clickMenu( index){
                this.$router.push(index)
            },*/
            commandHandler(cmd) {
                if (cmd == 'logout') {
                    this.$confirm('此操作将注销登录, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                     this.getRequset("/logout");
                        window.sessionStorage.removeItem("user")
                         this.$store.commit('initRoutes', []);

                        this.$router.replace("/");
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '取消操作'
                        });
                    });
                } else if (cmd == 'userinfo') {
                    this.$router.push('/hrinfo');
                }
            },
            goChat(){
                this.$router.push("/FriendChat")
            }
        },
        computed: {
            routes() {
                return this.$store.state.routes;
            }
        }
    }
</script>

<style scoped>

    .welcomeCls{
        font-size: 50px;
        text-align: center;
        margin-top: 100px;
        font-family: 华文仿宋;
        color: lightgreen;
    }
    .homeRouterView {
        margin-top: 10px;
    }

    .homeWelcome {
        text-align: center;
        font-size: 30px;
        font-family: 华文行楷;
        color: #409eff;
        padding-top: 50px;
    }

    .homeHeader {
        background-color: #409eff;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0px 15px;
        box-sizing: border-box;
    }

    .homeHeader .title {
        font-size: 30px;
        font-family: 华文行楷;
        color: #ffffff
    }

    .homeHeader .userInfo {
        cursor: pointer;
    }

    .el-dropdown-link img {
        width: 48px;
        height: 48px;
        border-radius: 24px;
        margin-left: 8px;
    }

    .el-dropdown-link {
        display: flex;
        align-items: center;
    }
    .homeRouter{
        margin-top: 20px;
    }
</style>