import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import 'font-awesome/css/font-awesome.min.css'

import {deleteRequset} from "./utils/apis";
import {postKeyValueRequest} from "./utils/apis";
import {postReqeust} from "./utils/apis";
import {putRequset} from "./utils/apis";
import {getRequset} from "./utils/apis";
import {initMenu} from "./utils/menus";

Vue.prototype.postReqeust=postReqeust;
Vue.prototype.postKeyValueRequest = postKeyValueRequest;
Vue.prototype.getRequset = getRequset;
Vue.prototype.deleteRequset = deleteRequset;
Vue.prototype.putRequset = putRequset;



Vue.config.productionTip = false



//全局前置守卫(类似于java的filter)
router.beforeEach((to, from, next) => {
    if (to.path == '/') {
        next();//放行
    } else {
        if(window.sessionStorage.getItem("user")){
            initMenu(router, store);
            next();
        }else{
            next('/');
        }


    }
})

Vue.use(ElementUI);

new Vue({
  router,
    store,
  render: h => h(App)
}).$mount('#app')
